.complete {
  background-color: green;
}
.incomplete {
  background-color: red;
}
.complete,
.incomplete {
  display: inline-block;
  width: 20px;
  height: 50px;
}
body {
  margin: 0;
  padding: 0;
}
.item {
  height: 50px;
  line-height: 50px;
  margin: 10px;
  border: 1px solid #9e9e9e;
  display: flex;
  justify-content: space-between;
  width: 50%;
}

.logo {
  width: 2rem;
  height:2rem;
}
